@import "../../variabls.scss";

.wrapper-slider {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (max-width: $sm) {
    .wrapper-slider {
        display: none;
    }
}

.content {
    display: flex;
    margin-top: 40px;
}

.container {
    display: flex;
    width: 1031px;
    overflow: hidden;
    margin: 0 20px;
}

@media screen and (max-width: $lg) {
    .container {
        width: 594px;
    }
}

@media screen and (max-width: $md) {
    .container {
        width: 450px;
    }
}

.baner {
    flex-shrink: 0;
    width: 100%;
    height: 400px;
    border-radius: 10px;
    background-size: cover;
    background-repeat: no-repeat;
    transition: all 0.3s ease;
}

@media screen and (max-width: $md) {
    .baner {
       height: 200px;
    }
}


.button { 
    background-color: $white;
    border: 1px solid $icons;
    cursor: pointer;
    width: 32px;
}

.button:hover {
    border: 1px solid $primary;
}

.slider-pointers {
    display: flex;
    margin-top: 18px;
}

.slider-point {
    width: 14px;
    height: 4px;
    background-color: $elements;
    margin:  0 14px;
    border: none;
    cursor: pointer;
}

.is-active {
  @extend .slider-point;
  background-color: $primary;
}